@use "global" as *;
.p-header {
    height: 80px;
    width: 100%;
    max-width: rem(1920);
}
.p-header__inner {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    padding: 0 rem(45);
    transition: all 0.5s;
    padding-top: rem(17.5);
    @include mq(md) {
        padding: rem(17.5) rem(30);
    }
}
.p-header-logo {
    width: 222px;
    height: 33px;
    padding-top: rem(1.5);

    @include mq(md) {
        width: rem(180);
    }
}
.p-header__pc-nav {
    @include mq(md) {
        display: none;
    }
}
.p-header__pc-nav-items {
    display: flex;
}
.p-header__pc-nav-item {
    font-size: rem(15);
    font-family: $font-family-base;
    margin-right: rem(20);
    font-weight: $bold;
    padding-top: rem(3);
    transition: all 0.5s;
}
.p-header__pc-nav-item:hover {
    opacity: 0.5;
}
.p-header__pc-nav-item:first-child {
    position: relative;
}
.p-header__pc-nav-item:first-child a::after {
    position: absolute;
    font-family: $font-family-base;
    bottom: rem(2);
    left: 50%;
    transform: translateX(-50%);
    content: "About";
    font-size: rem(14);
    display: block;
}
.p-header__pc-nav-item:nth-child(2) {
    position: relative;
}
.p-header__pc-nav-item:nth-child(2)a::after {
    position: absolute;
    font-family: $font-family-base;
    bottom: rem(2);
    left: 50%;
    transform: translateX(-50%);
    content: "Valuabe";
    font-size: rem(14);
}
.p-header__pc-nav-item:nth-child(3) {
    position: relative;
}
.p-header__pc-nav-item:nth-child(3)a::after {
    position: absolute;
    font-family: $font-family-base;
    bottom: rem(2);
    left: 50%;
    transform: translateX(-50%);
    content: "Works";
    font-size: rem(14);
}
.p-header__pc-nav-item:nth-child(4) {
    position: relative;
}
.p-header__pc-nav-item:nth-child(4)a::after {
    position: absolute;
    font-family: $font-family-base;
    bottom: rem(2);
    left: 50%;
    transform: translateX(-50%);
    content: "Flow";
    font-size: rem(14);
}
.p-header__pc-nav-item:nth-child(5) {
    position: relative;
}
.p-header__pc-nav-item:nth-child(5)a::after {
    position: absolute;
    font-family: $font-family-base;
    bottom: rem(2);
    left: 50%;
    transform: translateX(-50%);
    content: "Q&A";
    font-size: rem(14);
}
.p-header__pc-nav-item:nth-child(6) {
    margin-right: 0;
    padding-top: rem(1);
}
