@use "global" as *;
.gnav {
    background: $black;
    display: none;
    height: 100%;
    position: fixed;
    width: 100%;
    z-index: 10000;
    top: 0;
}
.gnav__wrap {
    align-items: center;
    display: flex;
    height: 100%;
    justify-content: center;
    position: absolute;
    width: 100%;
}
.gnav__menu {
    text-align: center;
}
.gnav__menu__item {
    margin: 40px 0;
}
.gnav__menu__item a {
    color: $white;
    font-size: rem(24);
    font-weight: bold;
    padding: 40px;
    text-decoration: none;
    transition: 0.5s;
}
.gnav__menu__item a:hover {
    color: #666;
}
