@use "global" as *;
.p-flow__title {
    position: relative;
}
.p-flow__title::after {
    position: absolute;
    bottom: rem(-7);
    left: 50%;
    transform: translateX(-50%);
    content: "制作の流れ";
    font-family: $font-family-base;
    font-size: rem(14);
    letter-spacing: 0.7;
}
.p-flow {
    padding: rem(100) 0 rem(100);
    position: relative;
}
.p-flow::before {
    position: absolute;
    width: 93%;
    height: 100%;
    top: 0;
    left: 0;
    content: "";
    background: $beige;
    z-index: -1;
    border-radius: 0px 100px 0px 0px;
    @include mq(md) {
        border-radius: 0px 50px 0px 0px;
        width: 100%;
    }
}
.p-flow__inner {
    text-align: center;
    padding-left: $padding-pc;
    padding-right: $padding-pc;
    @include mq(md) {
        padding-left: $padding-sp;
        padding-right: $padding-sp;
    }
}
.p-flow__text-top {
    padding-top: rem(30);
    display: inline-block;
    padding-bottom: rem(45);
    text-align: left;
    @include mq(md) {
        width: 82%;
    }
}
.p-flow__text {
    padding-top: rem(5);
    display: inline-block;
    font-size: rem(16);
    width: 100%;
    color: $black;

    @include mq(md) {
        font-size: rem(15);
        padding-top: rem(10);
    }
}
.p-flow__text span {
    font-weight: normal;
}
.p-flow__cards {
    text-align: left;
}
.p-flow__card {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    background: $white;
    width: 62.5%;
    max-width: rem(1200);
    margin-bottom: rem(40);
    margin-left: auto;
    margin-right: auto;
    padding: rem(20) rem(30) rem(20) rem(30);
    border-radius: 0px 50px 0px 0px;
    position: relative;
    filter: drop-shadow(0px 0px 22px rgba(0, 0, 0, 0.11));
    @include mq(md) {
        width: 100%;
    }
    @media screen and (max-width: 500px) {
        display: flex;
        flex-direction: column;
    }
}
.p-flow__card:last-child {
    margin-bottom: 0;
}
.p-flow-images {
    display: flex;
    align-items: center;
}
.p-flow__card:first-child:after {
    display: none;

    @media screen and (max-width: 500px) {
        display: block;
        position: absolute;
        width: 70px;
        height: 70px;
        top: 10px;
        left: 10px;
        content: "";
        background: url(../img/step-1.png) no-repeat center center / contain;

        width: 25%;
        height: 25%;
    }
}
.p-flow__card:nth-child(2):after {
    display: none;

    @media screen and (max-width: 500px) {
        display: block;
        position: absolute;
        width: 70px;
        height: 70px;
        top: 10px;
        left: 10px;
        content: "";
        background: url(../img/step-2.png) no-repeat center center / contain;

        width: 25%;
        height: 25%;
    }
}
.p-flow__card:nth-child(3):after {
    display: none;

    @media screen and (max-width: 500px) {
        display: block;
        position: absolute;
        width: 70px;
        height: 70px;
        top: 10px;
        left: 10px;
        content: "";
        background: url(../img/step-3.png) no-repeat center center / contain;

        width: 25%;
        height: 25%;
    }
}
.p-flow__card:nth-child(4):after {
    display: none;

    @media screen and (max-width: 500px) {
        display: block;
        position: absolute;
        width: 70px;
        height: 70px;
        top: 10px;
        left: 10px;
        content: "";
        background: url(../img/step-4.png) no-repeat center center / contain;

        width: 25%;
        height: 25%;
    }
}
.p-flow__number {
    height: auto;
    // width: rem(100);
    width: 5.20833vw;
    @include mq(md) {
        width: 10vw;
    }
    @media screen and (max-width: 500px) {
        display: none;
    }
}
.p-flow__number-figure {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 100%;
}
.p-flow__number-figure img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.p-flow__img {
    height: auto;

    width: 8.4375vw;
    margin-left: rem(30);
    margin-right: rem(30);
    @include mq(md) {
        width: 16vw;

        margin-right: rem(16);
        margin-left: rem(11);
    }
    @media screen and(max-width:500px) and(min-width:376px) {
        width: 26vw;
        margin-bottom: rem(10);
    }
    @include mq(sm) {
        width: 33vw;
    }
}
.p-flow__figure {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 100%;
}
.p-flow__figure img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.p-flow__sub-titles {
}
.p-flow__sub-title {
    font-size: rem(24);
    // font-weight: $bold;
    line-height: 1.07;
    color: $black;
    @include mq(xl) {
        font-size: rem(28);
    }
    @include mq(lg) {
        font-size: rem(18);
        font-weight: $bold;
    }
    @include mq(md) {
        font-size: rem(16);
    }
}
