@use "global" as *;
.p-works {
  padding: rem(100) 0 rem(100);
  position: relative;
  margin-bottom: rem(100);
  @include mq(md) {
    margin-bottom: rem(100);
    padding: rem(100) 0 rem(80);
  }
}
.p-works::after {
  background: $light-blue;
  position: absolute;
  width: 93%;
  height: 100%;
  top: 0;
  right: 0;
  z-index: -1;
  content: "";
  border-radius: 50px 0px 0px 0px;
  @include mq(md) {
    width: 100%;
  }
}

.p-works__wrappers-1 {
  overflow: hidden;
}
.p-works__sub-title {
  font-size: rem(36);
  font-family: $font-family-english;
  margin-top: rem(72);
  color: $black;
  @include mq(md) {
    font-size: rem(24);
  }
}
.p-works__sub-title-1 {
  margin-left: 76%;
  display: inline-block;
  position: relative;
  @include mq(lg) {
    margin-left: 65%;
  }
  @include mq(md) {
    margin-left: 60%;
  }
  @include mq(sm) {
    margin-left: 45%;
  }
}
.p-works__sub-title-1::after {
  position: absolute;
  width: 143%;
  height: 3px;
  top: 50%;
  transform: translateY(-50%);
  left: 105%;
  content: "";
  background: $green;
  overflow: hidden;
}
.p-works__sub-title-2 {
  margin-left: 15.5%;
  display: inline-block;
  position: relative;
}
.p-works__sub-title-2::after {
  position: absolute;
  width: 113%;
  height: 3px;
  top: 50%;
  transform: translateY(-50%);
  right: 105%;
  content: "";
  background: $green;
  overflow: hidden;
}
.p-works__sub-title-3 {
  margin-left: 76%;
  display: inline-block;
  position: relative;
  margin-top: rem(70);
  @include mq(lg) {
    margin-left: 73%;
  }
  @include mq(md) {
    margin-left: 60%;
    margin-top: rem(100);
  }
  @include mq(sm) {
    margin-left: 55%;
  }
}
.p-works__sub-title-3::after {
  position: absolute;
  width: 272%;
  height: 3px;
  top: 50%;
  transform: translateY(-50%);
  left: 105%;
  content: "";
  background: $green;
  overflow: hidden;
}
.wd-cards {
